
*:focus{outline:0
}
a{color:#2c3e50;text-decoration:none
}
a:hover,a:hover a{color:#1976D2
}
.clearfix::before{content:' ';display:table
}
.clearfix::after{clear:both
}
img{max-width:100%;max-height:100%
}
.app{font-family:'Avenir', Helvetica, Arial, sans-serif;font-size:18px;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#2c3e50;line-height:1.4
}
.app .header h1{margin-top:0;padding-top:20px;font-size:2.5em
}
.app .header .nav{list-style:none;text-align:center;padding-left:0
}
.app .header .nav li{display:inline-block;font-size:60px;position:relative
}
.app .header .zhihu{display:inline-block;margin-left:30px;position:relative;top:-10px;width:2em;line-height:2;font-size:26px;color:#fff;background:#2c3e50;border-radius:50%;transition:all .1s linear
}
.app .header .zhihu:after{content:'çŸ¥'
}
.app .header .zhihu:hover{color:#fff;background:#1976D2
}
.app .content{margin-bottom:60px;padding:0 15px
}
.app .footer{text-align:center;margin:30px 0
}
.particles-js-canvas-el{position:fixed;left:0;top:0;z-index:-99
}
@media (min-width: 900px){
.app .header{height:450px;background:transparent url(assets/bg.png) center 0 no-repeat;background-size:contain
}
.app .header h1{padding-top:60px
}
.content{width:960px;margin:0 auto
}
}
h1,h2{font-size:1.8em;text-align:center
}



